.UIInput{color:rgba(95,105,127,.55);background-image:url(../../../assets/icons/icons8-поиск.svg);background-repeat:no-repeat;background-position:26px 24px;height:0;max-width:240px;box-shadow:inset -6px -6px 10px rgba(255,255,255,.63),inset 6px 6px 20px 1px rgba(95,140,199,.33);border-radius:31px;transition:400ms;font-family:"Comfortaa";font-size:24px;font-weight:600;color:#5f697f}.UIInput::placeholder{font-family:"Comfortaa";font-size:24px;font-weight:600;color:#5f697f;color:rgba(95,105,127,.55);transition:200ms}.UIInput:hover{box-shadow:inset -6px -6px 25px rgba(255,255,255,.63),inset 6px 6px 20px 1px rgba(95,140,199,.2)}.UIInput:hover::placeholder{color:rgba(95,105,127,.301)}.UIInput:focus{box-shadow:inset -6px -6px 25px rgba(255,255,255,.63),inset 6px 6px 20px 1px rgba(95,140,199,.2)}.container{position:relative}.svg{position:absolute;z-index:1;top:24px;left:36px}